html {
  overflow-wrap: break-word;
  --page-size: a4 portrait;
  --page-margin: 1in;
  --orphans: 2;
  --widows: 2;
  margin: 0;
  padding: 0;
  font: 1em / 1.5 sans-serif;
}

body {
  --page-count: 0;
  background-color: #ddd;
  flex-direction: column;
  margin: 0;
  padding: 0;
  display: flex;
}

* {
  text-size-adjust: none;
}

body[aria-busy="true"] * {
  opacity: 0 !important;
}

body[aria-busy="true"]:after {
  content: "Generating…";
  text-align: center;
  font-size: 2em;
  position: fixed;
  top: calc(50vh - 2em);
  left: 0;
  right: 0;
}

.page {
  background-color: #fff;
  margin: 3em auto;
  box-shadow: inset 0 0 0 1px #000;
}

.page-inner {
  width: var(--page-width);
  height: var(--page-height);
  box-sizing: border-box;
  padding: var(--page-margin);
  flex-direction: column;
  flex-grow: 1;
  display: flex;
  overflow: hidden;
}

.page-content {
  flex-grow: 1;
  overflow: hidden;
}

.page-count:after {
  counter-reset: pagecount var(--page-count) pagenumber var(--page-number);
  content: counter(pagenumber) " of " counter(pagecount);
}

caption {
  break-after: avoid;
  break-inside: avoid;
}

thead {
  break-inside: avoid;
  break-after: avoid;
}

h1, h2, h3, h4, h5, h6 {
  break-inside: avoid;
}

@page {
  size: var(--page-size);
  margin: 0;
}

@media print {
  body {
    background-color: #0000;
    display: block;
  }

  * {
    break-before: auto !important;
    break-after: auto !important;
  }

  .page {
    box-shadow: none;
    height: 1px;
    display: block;
    overflow: visible;
    break-after: page !important;
    margin: 0 !important;
  }
}
